<template>
  <div id="loadingScreen">
    <h1 id="loadingText">LOADING.</h1>
  </div>
</template>

<script>
export default {
  name: "LoadingScreen",
  data() {
    return {
      counter: 0,
    };
  },
  methods: {
    load: function () {
      this.counter = this.counter % 3;

      if (this.counter == 0) {
        document.getElementById("loadingText").innerHTML = "LOADING.";
      } else if (this.counter == 1) {
        document.getElementById("loadingText").innerHTML = "LOADING..";
      } else if (this.counter == 2) {
        document.getElementById("loadingText").innerHTML = "LOADING...";
      }

      this.counter++;
    },
  },
  mounted() {
    window.setInterval(() => {
      if (document.getElementById("loadingScreen") == null) {
        window.clearInterval();
        return;
      }
      this.load();
    }, 1000);
  },
};
</script>

<style scoped>
#loadingScreen {
  position: absolute;
  background-color: white;

  width: 100%;
  height: 100%;
  transition: 1s opacity;
}

#loadingScreen.fade-out {
  opacity: 0;
}

#loadingText {
  position: relative;
  top: 50%;
  left: 50%;
  width: min-content;
  height: min-content;
  margin-top: -19px;
  margin-left: -75px;
  color: #000;
  font-family: "Courier New", Courier, monospace;
}
</style>