<template>
  <div id="canvasContainer"></div>
</template>

<script>
import * as THREE from "three";
// import { LineBasicMaterial } from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
// import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer.js";
// import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass.js";

export default {
  name: "ThreeCanvas",
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      ambientLight: null,
      directionalLight: null,
      controls: null,
      raycaster: null,
      mouse: new THREE.Vector2(),
      intersected: null,
      composer: null,
      outlinePass: null,
    };
  },
  methods: {
    init: function () {
      this.width = window.innerWidth;
      this.height = window.innerHeight;

      let canvas = document.getElementById("canvasContainer");

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x121212);

      const axesHelper = new THREE.AxesHelper(5);
      this.scene.add(axesHelper);

      this.camera = new THREE.PerspectiveCamera(
        50,
        this.width / this.height,
        0.1,
        1000
      ); //this is similar to the perspective matrix in opengl
      this.camera.rotation.y = (30 / 180) * Math.PI;
      this.camera.position.z = 0;
      this.camera.position.y = 10;
      this.camera.position.x = 10;

      const loadingManager = new THREE.LoadingManager(() => {
        const loadingScreen = document.getElementById("loadingScreen");
        loadingScreen.classList.add("fade-out");

        loadingScreen.addEventListener("transitionend", this.onTransitionEnd);
      });

      this.renderer = new THREE.WebGLRenderer({
        antialias: true,
      });
      this.renderer.physicallyCorrectLights = true;
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.shadowMap.enabled = true;
      this.renderer.setSize(this.width, this.height);
      this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
      this.renderer.shadowMap.width = 1024;
      this.renderer.shadowMap.height = 1024;

      this.raycaster = new THREE.Raycaster();

      this.controls = new OrbitControls(this.camera, this.renderer.domElement);

      const loader = new GLTFLoader(loadingManager);
      var self = this;

      this.initLights();
      this.loadModels(self, loader);
      this.setupRoom();

      // this.composer = new EffectComposer(this.renderer);

      this.controls.update();

      canvas.appendChild(this.renderer.domElement); //adds the render's DOM element(the element in the HTML tree structure) as the child of the canvas DOM element

      document.addEventListener("mousemove", this.onDocumentMouseMove);

      window.addEventListener("resize", this.onWindowResize);
    },
    setupRoom: function () {
      //Creates the three planes that acts as the walls of the room
      const floorGeometry = new THREE.PlaneBufferGeometry(30, 30, 30);
      const floorMaterial = new THREE.MeshPhongMaterial({
        side: THREE.FrontSide,
        color: 0xf2c081,
      });
      const floor = new THREE.Mesh(floorGeometry, floorMaterial);
      floor.side = THREE.DoubleSide;
      floor.receiveShadow = true;
      floor.rotation.x = (-90 / 180) * Math.PI;
      floor.position.x = 12;
      floor.position.y = -2.9;
      floor.position.z = -8;

      const wallMaterial = new THREE.MeshPhongMaterial({
        side: THREE.FrontSide,
        color: 0xffffff,
      });
      const wallGeometry = new THREE.PlaneBufferGeometry(30, 20, 30);
      const backWall = new THREE.Mesh(wallGeometry, wallMaterial);
      backWall.side = THREE.DoubleSide;
      backWall.receiveShadow = true;
      backWall.castShadow = true;
      backWall.rotation.y = (90 / 180) * Math.PI;
      backWall.position.x = -2.9;
      backWall.position.y = 7;
      backWall.position.z = -8;

      const leftWall = new THREE.Mesh(wallGeometry, wallMaterial);
      leftWall.side = THREE.DoubleSide;
      leftWall.receiveShadow = true;
      backWall.castShadow = true;

      leftWall.rotation.y = (180 / 180) * Math.PI;
      leftWall.position.x = 12;
      leftWall.position.y = 7;
      leftWall.position.z = 7;
      this.scene.add(floor);
      this.scene.add(backWall);
      this.scene.add(leftWall);
    },
    initLights: function () {
      this.ambientLight = new THREE.AmbientLight(0xffffff, 2);
      this.scene.add(this.ambientLight);

      this.directionalLight = new THREE.DirectionalLight(0xeedca5);
      this.directionalLight.position.set(10, 10, 0);
      this.directionalLight.castShadow = true;
      this.directionalLight.target.position.set(0, 0, 0);
      this.directionalLight.shadow.camera.far = 1000;
      this.directionalLight.shadow.mapSize.width = 2048;
      this.directionalLight.shadow.mapSize.height = 2048;
      const d = 10;
      //setting up the frustrum of the light
      this.directionalLight.shadow.camera.left = -d;
      this.directionalLight.shadow.camera.right = d;
      this.directionalLight.shadow.camera.top = d;
      this.directionalLight.shadow.camera.bottom = -d;
      this.scene.add(this.directionalLight);
    },
    loadModels: function (self, loader) {
      loader.load(
        "./desk.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.FrontSide;
            }
          });
          // scene.side = THREE.BackSide;

          // console.log(scene);

          const box = new THREE.Box3().setFromObject(gltf.scene); //centers the object at the origin
          const center = box.getCenter(new THREE.Vector3());
          gltf.scene.position.x += gltf.scene.position.x - center.x;
          gltf.scene.position.y += gltf.scene.position.y - center.y + 0.648;
          gltf.scene.position.z += gltf.scene.position.z - center.z;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
      loader.load(
        "./monitor.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.name = "Monitor";
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.FrontSide;
            }
          });

          // const box = new THREE.Box3().setFromObject(gltf.scene); //centers the object at the origin
          // const center = box.getCenter(new THREE.Vector3());
          gltf.scene.position.x += -1;
          gltf.scene.position.y += 2.95;
          gltf.scene.position.z += 4;

          gltf.scene.rotation.y = (120 / 180) * Math.PI;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
      loader.load(
        "./keyboard.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.FrontSide;
            }
          });

          gltf.scene.position.x += 1;
          gltf.scene.position.y += 2.95;
          gltf.scene.position.z += 2.5;
          gltf.scene.rotation.y = (10 / 180) * Math.PI;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
    },
    animate: function () {
      requestAnimationFrame(this.animate);
      if (
        this.intersected &&
        this.intersected.parent.name == "Monitor" &&
        this.intersected.parent.type == "Group"
      )
        this.controls.update();

      this.render();
    },
    onDocumentMouseMove: function (event) {
      //console.log("mouse moved");

      event.preventDefault();

      this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
    },
    onWindowResize: function () {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();

      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },
    render: function () {
      this.raycaster.setFromCamera(this.mouse, this.camera);
      const intersects = this.raycaster.intersectObjects(
        this.scene.children,
        true
      );

      if (
        intersects.length > 0 &&
        intersects[0].object.parent.name == "Monitor"
      ) {
        if (this.intersected != intersects[0].object) {
          if (this.intersected) {
            this.intersected.parent.translateY(-0.05);
          }

          this.intersected = intersects[0].object;
          this.intersected.parent.translateY(0.05);
        }
      } else {
        if (this.intersected) this.intersected.parent.translateY(-0.05);
        this.intersected = null;
      }

      this.renderer.render(this.scene, this.camera);
    },
    onTransitionEnd: function (event) {
      event.target.remove();
    },
  },
  mounted() {
    this.init();
    this.animate();
  },
};
</script>

<style scoped>
#canvasContainer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
/* h1 {
  position: relative;
  z-index: 1;
} */
</style>